import { toast } from "react-toastify";

export const hash = (e, mdp, setMdpHash, setCanCopy, crypType) => {
  if (crypType === "") {
    e.preventDefault();
    toast.error("Veuillez choisir un type de hash");

    return;
  }
  if (mdp === "") {
    e.preventDefault();
    toast.error("Veuillez entrez du texte");
    return;
  } else {
    e.preventDefault();
    setMdpHash(crypType(mdp));
    setCanCopy(true);
  }
};
export const copy = (mdpHash) => {
  navigator.clipboard.writeText(mdpHash);
  toast.success("Copie ffectu");
};
