import React, { useState } from "react";
import { ToastContainer, toast } from "react-toastify";
import "../styles/hash.css";
import "react-toastify/dist/ReactToastify.css";
import { hash, copy } from "../Function/Function";

const Hash = () => {
  let crypType = "";
  let [mdp, setMdp] = useState("");
  let [mdpHash, setMdpHash] = useState(
    "Le rsultat de votre hashage s'affichera ici"
  );
  let [canCopy, setCanCopy] = useState(false);
  const sha1Fun = () => {
    crypType = require("sha-1");
    toast.success("Vous avez choisir le hash SHA-1");
    return crypType;
  };
  const md5Fun = () => {
    crypType = require("md5");
    toast.success("Vous avez choisir le hash MD5");
    return crypType;
  };
  return (
    <div className="container">
      <div className="container-item">
        <ToastContainer theme="dark" />
        <div className="slogan">
          <h2>Bienvenue sur CrypMaster</h2>
          <p>Hasher vos mots de passe pour une meilleure scurit</p>
        </div>
        <div className="choice">
          <p>Choisissez un type de Hashage</p>
          <div>
            <button onClick={sha1Fun}>Sha-1</button>
            <button onClick={md5Fun}>md5</button>
          </div>
        </div>
        <form action="">
          <input
            type="text"
            placeholder="taper votre texte"
            onChange={(e) => setMdp(e.target.value)}
          />
          <button
            onClick={(e) => hash(e, mdp, setMdpHash, setCanCopy, crypType)}
          >
            Crypter
          </button>
        </form>
        <div className="render">
          <p>
            {mdpHash}
            <button
              className={canCopy ? "block" : "none"}
              onClick={() => copy(mdpHash)}
            >
              Copy
            </button>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Hash;
